import React from 'react';
import './section.css';
import { cssFactory, siteCss } from '../common/css';

import { addresse, telefon, email, sprechzeiten } from '../site/data';

const css = cssFactory('section');

function Kurse() {
  return (
    <div>
      <div className={css('heading')}>
        Kontakt
      </div>
      <div className={siteCss('par')}>
        Die Kurse finden an unserem Sitz in Saarbrcken statt (direkt links neben dem Haupteingang des Mercure-Hotels).
        <ul className={siteCss('offset')}>
          <li>Italienisch Lernen - Debora Difonzo</li>
          <li>{addresse.strasse}</li>
          <li>{addresse.ort}</li>
        </ul>
      </div>
      <div className={siteCss('par')}>
        <div className={css('sub-heading')}>Voranmeldung</div>
        Die unverbindliche Voranmeldung knnen Sie folgendermaen durchfhren:
        <ul className={siteCss('offset')}>
          <li>per E-Mail an <a href={`mailto:${email}?subject=Voranmeldung`}>{email}</a></li>
          <li>telefonisch unter {telefon} (Sprechzeiten: {sprechzeiten})</li>
          <li>oder persnlich nach Vereinbarung</li>
        </ul>
        Geben Sie dabei bitte den gewnschten Kurstermin an.
      </div>
    </div>
  );
}

export default Kurse;



