import React from 'react';
import './section.css';
import { cssFactory, siteCss } from '../common/css';
import programmAsHtmlTable from '../site/Programm/programm2react';
const css = cssFactory('section');

function Programm(props) {
  return (
    <div>
      <div className={css('heading')}>
        Programm {2018}
      </div>
      <div className={siteCss('par')}>
        Die Kurse finden an unserem Sitz in der Hafenstrae in Saarbrcken statt.
      </div>
      <div className={siteCss('par')}>
        {programmAsHtmlTable}
      </div>
      <div className={siteCss('par')}>
        Wenn Sie teilnehmen mchten, melden Sie sich <em>unverbindlich</em> fr den ersten Kurstermin an,
        per E-Mail, Telefon oder persnlich nach Vereinbarung.
        <br />
        Nach dem ersten Termin werden die Anmeldungen durchgefhrt.
        Sollte sich dabei ergeben, dass der Kurs nicht stattfindet, fallen fr Sie keinerlei Kosten an.
      </div>
    </div>
  );
}

export default Programm;




